	background: linear-gradient(
		180deg,
		rgba(171, 174, 183, 0) 0%,
		#abaeb7 51.56%,
		rgba(171, 174, 183, 0) 100%
	);
	display: inline-block;
} */

.project-lead {
	border-left: 1px solid
		linear-gradient(
			180deg,
			rgba(171, 174, 183, 0) 0%,
			#abaeb7 51.56%,
			rgba(171, 174, 183, 0) 100%
		);
	/* display: inline; */
}

.table-row {
	gap: 2rem;
}

.table-header {
	color: var(--color-primary-1);
}

.table-data {
	padding: 4rem;
	color: var(--color-white);
}

.table-row:hover > * {
	color: var(--color-primary-1);
}

.img-wrapper {
	max-height: 295px;
	padding: 0;
	margin-top: 1rem;
	margin-left: 1vw;
}

.img-wrapper img {
	width: 100%;
	height: 350px;
	object-fit: cover;
}

@media (max-width: 767px) {
	/* .container {
		width: 435px;
	} */

	.design-img {
		width: 100vw;
		margin: 1rem calc(-50vw + 50%);
	}

	.inner-container {
		max-width: 600px;
		margin: 0 auto;
		margin-top: 3rem;
		padding: 2rem;
	}
	.project-size {
		width: 400px;
	}

	.project-collection {
		width: 150px;
		height: 200px;
	}

	.heading-2 {
		font-size: 1rem;
	}

	.services {
		text-align: start;
		width: 100%;
		/* border: 1px solid blue; */
		margin: 0;
		margin-top: 3rem;
	}
	/* .about {
		width: 400px;
		margin-top: 3rem;
		padding: 1rem;
	} */

	.about .lead {
		padding: 0;
		margin: 0;
		border-left: none;
		font-size: 0.8rem;
		width: 100%;
	}

	.img-collection {
		width: 190px;
		height: 250px;
	}

	.review {
		margin-top: 6rem;
	}

	.review-heading {
		font-size: var(--fs-rem-6);
		color: var(--color-white);
		display: inline-block;
	}

	.about-client {
		display: inline-block;
		margin-left: 2rem;
		width: 2rem;
		font-size: var(--fs-rem-2);
		color: var(--color-white);
	}

	.about-img-1 {
		width: 100%;
		height: 100%;
	}

	.about-img-2 {
		display: none;
	}

	/* .project {
		width: 400px;
		margin-top: 3rem;
		padding: 1rem;
	} */

	.project ul {
		justify-content: space-around;
	}

	.project-collection {
		width: 190px;
		height: 250px;
	}
	/* 
	.carousel {
		width: 300px;
		height: 350px;
	} */

	.digital-R {
		display: none;
	}
}
